import React, {Component} from "react";
import PropTypes from "prop-types";
import OraTooltip from "../../../../core/components/ui/Tooltip";

export default class PriceRow extends Component{

    constructor(props){
        super(props);
    }

    componentDidMount() {
        OPL.UI.loadModules(document.getElementById(this.props.id), [{
            path: 'core/modules/tooltips',
            options: {
                side: "top",
                maxWidth: 300,
                mouseoverMinWidth: 0,
                triggerEvent: "mouseover"
            }
        }]);
    }

    render() {
        return (
            <div id={this.props.id} className="l-row u-animate-height js-layout-fixer-group-1 js-height-sensitive-element opl-messages--notifications-image u-no-padding">
                <div className="l-col l-col-6 u-padding-l u-padding-top-l u-padding-right u-text-right">
                    {
                        this.props.price.oneTimeCost &&
                        <Price price={this.props.price.oneTimeCost} net={this.props.net}/>
                    }
                </div>
                <div className="l-col l-col-6 u-padding-l u-padding-top-l u-padding-right u-text-right">
                    {
                        this.props.price.monthlyCosts && this.props.price.monthlyCosts.length > 0 && this.props.price.monthlyCosts[0] &&
                        <Price price={this.props.price.monthlyCosts[0]} tooltip={this.props.tooltip} net={this.props.net}
                               priceSteps={this.props.price.monthlyCosts.length && this.props.price.monthlyCosts.length > 1}/>
                    }
                </div>
            </div>
        );
    }
}

PriceRow.propTypes = {
    price: PropTypes.shape({
        oneTimeCost: PropTypes.shape({
            gross: PropTypes.string,
            net: PropTypes.string,
            currency: PropTypes.string
        }),
        monthlyCosts: PropTypes.arrayOf(
            PropTypes.shape({
                gross: PropTypes.string,
                net: PropTypes.string,
                currency: PropTypes.string,
                monthFrom: PropTypes.number,
                monthTo: PropTypes.number
            })
        )
    }),
    id: PropTypes.string.isRequired,
    tooltip: PropTypes.string,
}

PriceRow.defaultProps = {
    tooltip: "Abonament zawiera rabaty za e-fakturę 5,01 zł i zgodę marketingową 5,01 zł. Zgody te możesz wyrazić na kolejnym kroku zamówienia. Jeśli ich nie wyrazisz rabaty nie będą naliczone.",
}

export const Price = (props) => {
    const price = props.net ? props.price.net.split(',') : props.price.gross.split(',');
    const integerPricePart = price[0];
    const fractionPricePart = price[1] ? price[1].split(' ')[0] : "00";
    const monthly = !!props.price.monthFrom || !!props.price.monthTo;

    return (
        <div>
            <span className="u-font-xlarge u-font-bold u-no-spacing">{integerPricePart}</span>
            <span className="u-font-large u-font-bold u-no-spacing">,{fractionPricePart}</span>
            <span className="u-font-large u-font-bold u-spacing-left-s">{`${props.price.currency}${props.net ? ' + VAT' : ''}`}</span>
            {
                props.price.installments && props.price.monthTo && props.priceSteps &&
                <span className="u-font-large u-font-bold u-no-spacing u-small-hide"> x {props.price.monthTo} raty</span>
            }
            {
                monthly &&
                <span className="u-large-hide u-medium-hide u-font-large u-font-bold u-no-spacing">/mies.</span>
            }
            {
                props.tooltip &&
                <OraTooltip>{props.tooltip}</OraTooltip>
            }
        </div>
    )
}

Price.propTypes = {
    price: PropTypes.shape({
        gross: PropTypes.string.isRequired,
        currency: PropTypes.string.isRequired,
        monthFrom: PropTypes.number,
        monthTo: PropTypes.number,
        installments: PropTypes.bool,
    }),
    tooltip: PropTypes.string,
}